
import localforage from 'localforage'
import {post} from "./method.js" 

import Toast from 'vux/src/components/toast/index.vue'

export default {
  data () {
    return {
      userName: '朱光晨',
      password:'123456',
      step:'one',
      promptText:'第一步:输入您的用户名和密码',
      selectList:null,
      usbkeyidentification:null,
      userPoint:'用户名',
      userNameError:false,
      passWordPoint:'密码',
      passWordError:false,
      showAlert:false,//控制提醒框是否显示
      textAlert:'',//弹出框显示文字
      showPositionValue:true
    }
  },
  components: {
    Toast
  },
  methods: {
    loginIn: function(){
      const _this = this;
      const postData={userName:this.userName,password:this.password};
      if(_this.passWordError || _this.userNameError){

        _this.textAlert = '账号密码没有输入'
        _this.showAlert = true
      }
      else{
        //登陆请求
        post("http://localhost:9999/nameLoginList",postData,function(data){
          //判断是否取到数据
          if(data !=="" && data !==null){
            const Data = JSON.parse(data);
            //document.write(data);
            if(data === "[]"){
              window.location.href="#/Main"
            }
            else{
              this.promptText = '第二步:请选择所属组织';
              _this.selectList=Data
            }
          }
          else{
            _this.textAlert = '与服务器连接出现问题！'
            _this.showAlert = true
          }
        });
      }
    },
    jump:function(name,num,idCard){
      const _this = this;
      const data={usbkeyidentification:idCard,password:this.password};
      //把用户名存储到起来
      localforage.setItem('userName', name, function (err){
        
      });
      localforage.setItem('usbkeyidentification', idCard, function (err){
        
      });
      post("http://localhost:9999/login",data,function(d){
        const Data = JSON.parse(d);
      });
      window.location.href="#/Main"
    },
    checkUserName:function(){
      //判断用户名是否正确
      if(this.userName === ''){
        this.userPoint = '用户名不能为空！'
        this.userNameError = true
      }
      else{
        this.userPoint = '用户名'
        this.userNameError = false
      }
    },
    checkPassWord:function(){
      //判断用户名是否正确
      if(this.userName === ''){
        this.passWordPoint = '密码不能为空！'
        this.passWordError = true
      }
      else{
        this.passWordPoint = '密码'
        this.passWordError = false
      }
    },
    closeAlert:function () {
      //将弹出框隐藏
      this.showAlert = false
    }
  },
}



